






























import Vue from 'vue';
import {Component} from 'vue-property-decorator';

@Component
export default class NumberPad extends Vue {
  output = '0';
  result=0
  inputContent(event: MouseEvent) {
    const button = (event.target as HTMLButtonElement); //强制指定类型
    const input = button.textContent as string;
    // const input=button.textContent!
    if (this.output.length === 16) {
      return;
    }
    if (this.result !== 0) {
      this.result = 0;
      this.output=''
    } else if (this.output === '0') {
      if ('0123456789'.indexOf(input) >= 0) {
        this.output = input;
      } else {
        this.output += input;
      }
      return;
    }
    if (this.output.indexOf('.') >= 0 && input === '.') {
      return;
    }
    this.output += input;
  }

  remove() {
    if (this.output.length === 1) {
      this.output = '0';
    } else {
      this.output = this.output.slice(0, -1);
    }

  }

  clear() {
    this.output = '0';
  }

  add() {
    this.output+='+'
  }

  reduce() {
    this.output+='-'
  }
  ok() {
    if(this.output==='0'){window.alert('请输入金额'); return}
    if (this.output[this.output.length - 1] === '+' || this.output[this.output.length - 1] === '-') {
      window.alert('计算错误');
      this.output = '0';
      return
    } else {
      this.result = eval(this.output);
      this.output = this.result.toString();
    }
    this.$emit('update:value', this.result);
    this.$emit('submit', this.result);
    this.output = '0';

  }


}

